.controlBox {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
  .button {
    @include font-line(13, 20);
    border: 1px solid $color-accent;
    cursor: pointer;
    width: 35%;
    height: 58px;
    font-weight: 700;
    color: $color-accent;
    letter-spacing: 0.04em;
    text-align: center;
    text-decoration: none;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .checkButton {
    $allCheck: '.checkButton';
    background: $color-accent;
    color: #fff;
    &__input {
      height: 26px;
      width: 26px;
      &:checked + #{$allCheck}__parts {
        &::after {
          @include font-line(26);
          height: 26px;
          width: 26px;
          top: 0;
        }
      }
    }
    &__parts {
      padding-left: 36px;
      &::before {
        border: 0;
        height: 26px;
        width: 26px;
      }
    }
    &__down {
      &:before{
        content: "";
        position: absolute;
        top: 100%;
        left: 50%;
        margin-left: -15px;
        border: 10px solid transparent;
        border-top: 10px solid $color-accent;
      }
    }
    &__up {
      &:before{
        content: "";
        position: absolute;
        bottom: 100%;
        left: 50%;
        margin-left: -15px;
        border: 10px solid transparent;
        border-bottom: 10px solid $color-accent;
      }
    }
  }
  .go_inquiry {
    border-color: $color-accent;
  }
  .deleteButton {
    border-color: $color-accent;
    width: 27%;
  }
}

/*リスト表示部分*/
.bukkenList {
  $bukkenList: ".bukkenList";
  margin: 0;
  .bukkenItem {
    border-top: 2px solid #666;
    padding: 0;
    margin-bottom: 20px;
    &__header {
      background-color: #fff;
      margin-bottom: 20px;
      padding: 0;
      .stamps {
        height: 13px;
        margin: 10px 0;
        .type {
          background-color: transparent;
        }
        .new {
          background-color: $color-sub;
          border: none;
          color: #fff;
        }
        .pricedown {
          background-color: $color-main;
          border: none;
          width: 74px;
          color: #fff;
        }
      }
      .title {
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .heading {
          margin: 0;
          line-height: 13px;
          .check {
            &__input {
            }
            &__parts {
              font-weight: 700;
              @include font-line(13, 13);
              &:before {
                margin-top: 0;
                top: 3px;
              }
              &:after {
                bottom: unset;
                top: 1px;
              }
            }
          }
        }
      }
    }
    .groupButton {
      margin: 0 0 10.5px;
      display: flex;
      justify-content: space-between;
      .button {
        width: 49%;
        height: 35px;
        @include font-line(12, 12);
        i {
          padding-right: 5px;
        }
        &::after {
          display: none;
        }
      }
    }
    &__body {
      border: none;
      padding:0;
      text-decoration: none;
      display: block;
    }
    &__thumb {
      width: 100%;
      height: 225px;
      margin: 0 auto;
      position: relative;
      img {
        width: 100%;
        height: 100%;
        object-fit: contain;
        font-family: 'object-fit: contain;;';
      }
      .stamp {
        width: 97px;
        height: 97px;
        border-radius: 50%;
        background-color: #000;
        color: #fff;
        @include font-line(13, 13);
        @include opacity(0.6);
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        margin: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        position: absolute;
        z-index: 2;
      }
    }
    &__info {
      margin-top: 7px;
      color: $color-text01;
    }
  }
}
